.Toolbar {
    height: 3em;
    width: 100vw;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #ccc;
    border-bottom: 3px solid #36f0e3;
}

.Toolbar > *:first-child {
    -webkit-filter: drop-shadow(0.1em 0.1em 0.05em rgba(0,0,0,1));
}

.LogOut {
    cursor: pointer;
    text-decoration: underline;
    color: #000;
}